<template>
  <div v-if="display" id="loadingContainer">
    <div class="textContainer">
       <div class="loading">
    <div class="circle circle1">
        <span></span>
        <span></span>
        <span></span>
        <span></span>
    </div>
    <div class="circle circle2">
        <span></span>
        <span></span>
        <span></span>
        <span></span>
    </div>
    <div class="circle circle3">
        <span></span>
        <span></span>
        <span></span>
        <span></span>
    </div>
 </div>
      <div class="text">{{ tips }}</div>
    </div>
  </div>
</template>

<script>
export default {
  name: "FullScreenLoading",

  data () {
    return {
      display: false,
      tips: "",
      opTips: "加载中..."
    }
  },

  methods: {
    show(tips) {
      this.tips = tips || this.opTips;
      this.display = true;
    },

    hide() {
      this.display = false;
    }
  }
};
</script>

<style lang="scss" scoped>
#loadingContainer {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background-color: rgba($color: #000000, $alpha: 0.5);

  .textContainer {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 200px;
    height: 200px;
    margin: -100px auto 0 -100px;
    text-align: center;
    color: rgb(4, 100, 211);
  }

   .loading{
            width:30px;
            height:30px;
            margin:20px auto;
            position:relative;
        }
        .circle{
            position:absolute;
            top:0;
            left:0;
            width:100%;
            height:100%;
        }
        .circle span{
            width:8px;
            height:8px;
            display:inline-block;
            background:rgb(4, 100, 211);
            border-radius: 100%;
            position:absolute;
            -webkit-animation: mycircle 1.2s infinite ease-in-out;
            animation: mycircle 1.2s infinite ease-in-out;
            -webkit-animation-fill-mode:both;
            animation-fill-mode:both;
        }
       .circle2{
           -webkit-transform: rotateZ(45deg);
           transform: rotateZ(45deg);
       }
       .circle3{
           -webkit-transform: rotateZ(90deg);
           transform: rotateZ(90deg);
       }
       .circle>span:nth-child(1){
            top:0;
            left:0;
       }
       .circle>span:nth-child(2){
            top:0;
            right:0;
       }
       .circle>span:nth-child(3){
            right:0;
            bottom:0;
       }
       .circle>span:nth-child(4){
            left:0;
            bottom:0;
       }
       .circle2 >span:nth-child(1){
            -webkit-animation-delay: -1.1s;
            animation-delay: -1.1s;
       }
       .circle3 >span:nth-child(1){
            -webkit-animation-delay: -1.0s;
            animation-delay: -1.0s;
       }
       .circle1 >span:nth-child(2){
            -webkit-animation-delay: -0.9s;
            animation-delay: -0.9s;
       }
       .circle2 >span:nth-child(2){
            -webkit-animation-delay: -0.8s;
            animation-delay: -0.8s;
       }
       .circle3 >span:nth-child(2){
            -webkit-animation-delay: -0.7s;
            animation-delay: -0.7s;
       }
       .circle1 >span:nth-child(3){
            -webkit-animation-delay: -0.6s;
            animation-delay: -0.6s;
       }
       .circle2 >span:nth-child(3){
            -webkit-animation-delay: -0.7s;
            animation-delay: -0.7s;
       }
       .circle3 >span:nth-child(3){
            -webkit-animation-delay: -0.4s;
            animation-delay: -0.4s;
       }
       .circle1 >span:nth-child(4){
            -webkit-animation-delay: -0.3s;
            animation-delay: -0.3s;
       }
       .circle2 >span:nth-child(4){
            -webkit-animation-delay: -0.2s;
            animation-delay: -0.2s;
       }
       .circle3 >span:nth-child(4){
            -webkit-animation-delay: -0.1s;
            animation-delay: -0.1s;
       }
       @-webkit-keyframes mycircle{
            0%{transform:scale(0.0);}
            40%{transform:scale(1.0);}
            80%{transform:scale(0.0);}
            100%{transform:scale(0.0);}
        }
        @keyframes mycircle{
            0%{transform:scale(0.0);}
            40%{transform:scale(1.0);}
            80%{transform:scale(0.0);}
            100%{transform:scale(0.0);}
        }
}
</style>
